@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100&display=swap');
* { font-family: 'Montserrat', sans-serif; background:black; margin:0; padding: 0;}

html { box-sizing:border-box; height: 100%;}
Link { text-decoration:none; }
ul { list-style:none;}
// 좌측 메뉴
.nav { width: 80px; position:fixed; left:0; top:0; height:100%; color:white; z-index:3;}

.logo p { transform: rotate(90deg); font-size:25px; padding-left:50px; background:none;
    font-weight:bold;}

.nav {
    .logo {
        border:1px solid white;
        height: 95%
       
    }
    .menu {
        border:1px solid white;
        height: 5%;
        padding-top:30px;
        cursor: pointer;
    }
}

.menuani { width:900px; position:fixed; top:0; border: 1px solid white;}
.menuani {
    li { border:1px solid white; padding:25px 28px; font-size:18px; font-weight:bold; text-align: left;}
    p:hover { color:#e2d3be}
}
.menu1, .menu2 {width:900px; position:fixed; top:0; animation: 0.5s ease-in-out loadEffect1;  animation-fill-mode: forwards; height: 100%; border:1px solid white;}
.menu1 li { border:1px solid white; padding:27px 28px; font-size:18px; font-weight:bold; text-align: left;}
.profilebox{ display:flex;}
.profile {  margin:50px auto; display: block;}
.menu1 h1 { font-size:90px; font-weight:bold; text-align:left; padding-top:50px; padding-left:50px;}
.profile img:hover { transform: scale(1.1); transition:all 0.5s;}
.menuani{ animation: 0.5s ease-in-out loadEffect1;  animation-fill-mode: forwards; height: 100%;}
.introduce { margin-top:20px;}
.introduce h3 { font-size:20px; margin-bottom:10px;}
.project h3 { padding-top:50px; padding-left:50px;text-align:left;}
@keyframes loadEffect1 {
    0%{
        opacity: 0;
        transform:translateX(0px);
        display:none;
    }
    100%{
        opacity: 1;
        transform:translateX(79px);
    }
}


.intro { color: white; font-weight:bold; text-align:left; padding:200px;letter-spacing:-1px;
    padding-top:300px; position: relative; height: 100%;}
.intro {
    h1 { font-size:101px;}
    p { font-size:30px; padding-top:30px;}
    .anibox {
        position:absolute;
        right:50px;
        bottom:0;
        display:inline-block;
    }
    img { width:50px;}
}
.scroll {  white-space:nowrap; width:425vw; }
.scrollgogh { margin: 0 auto; overflow:auto; white-space:nowrap; width: 640vw;}
.scrollmonet {  margin: 0 auto; overflow:auto; white-space:nowrap;width: 710vw;}
.slide-box {float:left; color:white; }
.last { position: relative;}
.last p { color: white; font-size:58px; font-weight:bold; display:block; position:absolute; top:40%; left:0; background: none; width:900px; text-align:left; letter-spacing:-1px; }

.slidepicture { display:flex; color:white; margin: 300px 300px 300px 350px;}
.slidepicture  img { padding-right:180px;}

.linebox { width:400px; height: 400px; border:2px solid #AB8B62; position: relative; margin:7.5vh 30px; background:none;}

.eye { background-size:100%; background-repeat:no-repeat; background-image:url(../public/img/ani-icon-eye.gif); position:absolute; left:40%; top:-3rem; height:6rem; width: 6rem;}
.eyebox { position: fixed; top:170px;left:200px; transition:all 1s ease; background:none; display:none;}
.tail { background-color:white !important; width:50vw; color:black; height:98vh; margin-left:380px; position: relative;}
.tailtext { position:absolute; top:35%; left:100px; background-color:white;}
.tailtext {
    h2,button,p,.button { background-color:white;text-align:left; }
    p { font-size:20px; margin-top:40px; font-weight:100;}
    h2 { font-size:65px;}
    button { font-size:20px; margin-top:50px; border-radius:5px; font-weight:bold;}
    .button2 { 
        background-color: #AB8B62;
        background-image: linear-gradient(to right, #AB8B62 0%, #eacda3 50%, #AB8B62 100%);
        background-size: 200% auto;
        border: 1px solid #AB8B62;
        transition: 0.5s ease; padding:10px 60px;}
    .button1 { 
        border: 1px solid #111111;
        opacity: 0.5;
        background-color: transparent; margin-right:30px; padding:10px 30px; }
    .button1:hover { opacity:1;}
}

.gogh { border:5px solid #AB8B62;}
.gogh img { height:97vh; }
// .goghpicture img { width: 100%;}
.goghpicture { display:flex;}

.museuminfo { border:1px solid white; }
.imagecontainer { border: 1px solid white;}
.index { padding:476px; width: 5vh;background:white; }
.index p {  color:black; display:block; margin-left:200px; font-size:75px;  font-weight:400; letter-spacing:5px;}
.index span { background:none; font-size:50px; margin-left:160px;}
.artwork {display:flex; padding:158.5px; background:white !important;}
.artwork2 {display:flex; padding:174px; background:white !important;}
.artwork,.artwork2 {
    .imagecontainer, .museuminfo, .museuminfo h6,p  {
        background-color:white !important;
    }
    .museuminfo h6,p { color:black;}
}
.museuminfo {
    h6 { font-size:23px; font-weight:400;}
    p { margin-top:20px;font-size:20px; font-weight:100;}
}

.museuminfo { margin:40px 30px; }

